<template>
  <div><br><br><br>
    <v-layout class="text-xs-center">
      <v-flex>
        <v-btn class="text-xs-center" @click='sendPush'>Push Notification</v-btn>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import axios from 'axios'
var vapidPublicKey = new Uint8Array([4, 0, 159, 53, 105, 117, 82, 165, 178, 201, 17, 200, 195, 188, 17, 110, 109, 25, 31, 37, 88, 138, 120, 60, 230, 192, 78, 84, 204, 230, 203, 154, 195, 34, 202, 126, 58, 42, 115, 39, 184, 135, 27, 116, 237, 83, 207, 191, 159, 214, 210, 218, 77, 150, 123, 49, 102, 183, 219, 192, 180, 45, 49, 244, 26]);
// var vapidPublicKey = new Uint8Array(<%=Base64.urlsafe_decode64($vapid_public).bytes%>);

export default {
  data: () => ({

  }),
  methods: {
    sendPush () {
      var sub = {"subscription":{"endpoint":"https://fcm.googleapis.com/fcm/send/dRPEQE0tjUE:APA91bElSzQvAvOapx3mKX9bJnV_2NehKAvqe6DY8t_l0WWb6GTqIMNBcBhDo1yYuYEr4FQxwXcBKig3VDXsnkJhj8n3l3K_exB_-dNHqmBdMAAKNmKRaTwVNWkWEkdGxy5FC2zy_-xq", "expirationTime":"", "keys":{"p256dh":"BHy1R_6K_Liy_rHsMBBhZDNkDyY0l4FanWZdhsRWaLFsIzqrieF9b0uDOpi0wcx5djuDznOt--dsTTk7kI21ciI", "auth":"jQDOj4RzRlpRiRg_-WUR3A"}}, "message":"You clicked a button!"}
      console.log(sub['subscription'])
      axios({
        method: 'post',
        url: this.$hostname + '/sendNo',
        data: {'subscription': sub['subscription'], message: 'You clicked a button!'}
      }).then(response => {
        console.log(response)
      }).catch(error => {
        console.log(error)
      })
    },
    	// sendPush(){
    	// 	this.getKeys(false)
    	// },
     checkNotifs(obj){
       if (!("Notification" in window)) {                                             //1
            console.error("This browser does not support desktop notification");
          }
          // Let's check whether notification permissions have already been granted
          else if (Notification.permission === "granted") {                           //2
            console.log("Permission to receive notifications has been granted");
            getKeys()
          }
          // Otherwise, we need to ask the user for permission
          else if (Notification.permission !== 'denied') {                            //3
            Notification.requestPermission(function (permission) {
                  // If the user accepts, let's create a notification
              if (permission === "granted") {                                         //4
                console.log("Permission to receive notifications has been granted");
                getKeys();
              }
            });
         }
    },

       getKeys(){
        navigator.serviceWorker.register('https://www.evossol.com/serviceworker.js', {scope: './'})          //5
          .then(function(registration) {
            return registration.pushManager.getSubscription()
              .then(function(subscription) {
                if (subscription) {
                  return subscription;
                }
                return registration.pushManager.subscribe({                           //6
                  userVisibleOnly: true,
                  applicationServerKey: vapidPublicKey
                });
              });
          }).then(function(subscription) {
          		sendNo(subscription.toJSON())
          });
      },

       sendNo(subscription){
    		console.log(subscription)
    	$.post('/sendNo', {
        	subscription: subscription,
            message: 'You clicked a button!'
         });
      }
  }
}
</script>



