// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(Vuetify)
Vue.use(VueAxios, axios)
Vue.config.productionTip = true
Vue.prototype.$hostname =
// (Vue.config.productionTip) ? 'https://sanu-webpush.herokuapp.com' : 'http://localhost:3000'
(Vue.config.productionTip) ? 'http://ec2-18-222-228-96.us-east-2.compute.amazonaws.com:3000' : 'http://localhost:3000'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



